// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import vpaging from './vpaging'
import iView from 'iview'
import Vuex from 'vuex'
import axios from "axios"
import './css/index.css'
import VRouter from 'vue-router'
import store from '../../vuex/Pstore.js'
import 'iview/dist/styles/iview.css'
import register from '../CenterSide/Register/Register'
import enroll from '../CenterSide/Register/enroll'
import routers from '../CenterSide/module/router'
Vue.prototype.axios = axios
Vue.config.productionTip = false
Vue.use(VRouter)
Vue.use(iView)
Vue.use(Vuex)
let router = new VRouter({

  routes:[
   
	{
     path:"/user",
	 component:register
 },{
	  path:"/indexpage",
      component:routers
	 },
		  {
	  path:"/enroll",
      component:enroll
	 }
  ]
})
/* eslint-disable no-new */
new Vue({
      el: '#Mono',
	  store,
	  router,
      template: '<vpaging/>',
      components: { vpaging },
	  data:{
     eventHub:new Vue()   // ����ֱ�ӵ���
     }
	
})



