import Vue from 'vue'
import Vuex from 'vuex'
import axios from "axios"
Vue.prototype.axios = axios
Vue.use(Vuex)

/// 分类导航模块
const CateGory = {
  state:{
   
    bgImg:""
  },

actions:{
	//背景获取的数据
     bgImg({commit}){
   
        axios.get("http://localhost:8080/api/img/i")
	    .then( (res) => {

	   
			   commit("bgImg",res.data.Im)
	     })
	    .catch(function(res){
	       console.log(res)
	    })   
	  
	 },
  },

mutations: {
   bgImg:function(state,Bg){
	
     state.bgImg = Bg
		 
  }
   
   }
}
const store = new Vuex.Store({
 modules:{
   CateGory:CateGory
 }

})


export default store



