<template>
    <Form ref="formCustom" :model="formCustom" :rules="ruleCustom" :label-width="80">
        <Form-item label="����" prop="passwd">
            <Input type="password" v-model="formCustom.passwd"></Input>
        </Form-item>
        <Form-item label="ȷ������" prop="passwdCheck">
            <Input type="password" v-model="formCustom.passwdCheck"></Input>
        </Form-item>
        <Form-item label="����" prop="age">
            <Input type="text" v-model="formCustom.age" number></Input>
        </Form-item>
        <Form-item>
            <Button type="primary" @click="handleSubmit('formCustom')">�ύ</Button>
            <Button type="ghost" @click="handleReset('formCustom')" style="margin-left: 8px">����</Button>
        </Form-item>
    </Form>
</template>
<script>
    export default {
        data () {
            const validatePass = (rule, value, callback) => {
                if (value === '') {
                    callback(new Error('����������'));
                } else {
                    if (this.formCustom.passwdCheck !== '') {
                        // �Եڶ��������򵥶���֤
                        this.$refs.formCustom.validateField('passwdCheck');
                    }
                    callback();
                }
            };
            const validatePassCheck = (rule, value, callback) => {
                if (value === '') {
                    callback(new Error('���ٴ���������'));
                } else if (value !== this.formCustom.passwd) {
                    callback(new Error('�����������벻һ��!'));
                } else {
                    callback();
                }
            };
            const validateAge = (rule, value, callback) => {
                if (!value) {
                    return callback(new Error('���䲻��Ϊ��'));
                }
                // ģ���첽��֤Ч��
                setTimeout(() => {
                    if (!Number.isInteger(value)) {
                        callback(new Error('����������ֵ'));
                    } else {
                        if (value < 18) {
                            callback(new Error('��������18��'));
                        } else {
                            callback();
                        }
                    }
                }, 1000);
            };
            
            return {
                formCustom: {
                    passwd: '',
                    passwdCheck: '',
                    age: ''
                },
                ruleCustom: {
                    passwd: [
                        { validator: validatePass, trigger: 'blur' }
                    ],
                    passwdCheck: [
                        { validator: validatePassCheck, trigger: 'blur' }
                    ],
                    age: [
                        { validator: validateAge, trigger: 'blur' }
                    ]
                }
            }
        },
        methods: {
            handleSubmit (name) {
                this.$refs[name].validate((valid) => {
                    if (valid) {
                        this.$Message.success('�ύ�ɹ�!');
                    } else {
                        this.$Message.error('������֤ʧ��!');
                    }
                })
            },
            handleReset (name) {
                this.$refs[name].resetFields();
            }
        }
    }
</script>


